.wrapper {
  padding-top: 50px;
  background: -webkit-gradient(linear, left top, left bottom, from(navy), color-stop(blue), to(teal));
  background: -webkit-linear-gradient(navy, blue, teal);
  background: -o-linear-gradient(navy, blue, teal);
  background: linear-gradient(navy, blue, teal);
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  padding: 20px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  overflow: auto;
}



