.card {
  background: rgb(50, 40, 133);
  margin-left: 50px;
  border: 20px;
  border-color: black;
  border-radius: 10px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
  -webkit-box-shadow: 6px 6px 12px slategray;
          box-shadow: 6px 6px 12px slategray;
  text-align: left;
}

.card > .img-container {
  border-radius: 10px;
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: -webkit-radial-gradient(rgb(22, 11, 61), rgb(54, 178, 187));
  background: -o-radial-gradient(rgb(22, 11, 61), rgb(54, 178, 187));
  background: radial-gradient(rgb(22, 11, 61), rgb(54, 178, 187));
}

.card > .img-container > img {
  width: 80%;
}

.card > .img-container:hover {
  -webkit-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
          transform: rotate(360deg);
  /* transform: scale(1.3); */
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  background: -webkit-radial-gradient(navy, teal);
  background: -o-radial-gradient(navy, teal);
  background: radial-gradient(navy, teal);
}



